import { mapActions } from "vuex";

export default {
  data() {
    return {};
  },
  methods: {
    ...mapActions(["authAction", "getAllProduct","setCurrentProduct"]),
    
  },
  mounted() {
    this.authAction();
    this.getAllProduct();
    this.setCurrentProduct();
  },
};
